.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
}

.header-left {
  display: flex;
  align-items: center;
}

.search-bar {
  display: flex;
  align-items: center;
  border-bottom: 1px solid black;
}

.search-bar i {
  margin-right: 5px;
}

.search-bar input {
  border: none;
  outline: none;
  padding: 5px;
}

.header-center {
  text-align: center;
}

.header-right {
  display: flex;
  gap: 20px;
  align-items: center;
}

.header h1 {
  font-size: 24px;
  font-weight: bold;
}
