.product-carousel {
   width: 80%;
   margin: auto;
   position: relative;
 }
 
 .product-card {
   text-align: center;
   padding: 20px;
   background-color: #f9f9f9;
   border-radius: 10px;
   position: relative;
 }
 
 .product-card img {
   width: 100px;
   height: auto;
 }
 
 .product-card h3 {
   margin: 10px 0;
   font-size: 18px;
 }
 
 .product-card p {
   font-size: 16px;
 }
 
 .old-price {
   text-decoration: line-through;
   color: red;
   margin-right: 5px;
 }
 
 .new-price {
   color: green;
   font-weight: bold;
 }
 
 .discount {
   position: absolute;
   top: 10px;
   left: 10px;
   background-color: green;
   color: white;
   padding: 5px;
   border-radius: 5px;
 }
 
 .custom-arrow {
   position: absolute;
   top: 50%;
   transform: translateY(-50%);
   font-size: 24px;
   color: black;
   padding: 10px;
   border-radius: 50%;
   cursor: pointer;
   z-index: 1;
 }
 
 .next-arrow {
   right: -40px; /* 화살표를 오른쪽 바깥쪽으로 */
 }
 
 .prev-arrow {
   left: -40px;  /* 화살표를 왼쪽 바깥쪽으로 */
 }