/* MenuNavbar.css */
.menu-navbar {
   width: 100%;
   background-color: black;
   padding: 10px 0;
 }
 
 .menu-navbar-menu {
   list-style: none;
   display: flex;
   justify-content: center;
   gap: 30px;
 }
 
 .menu-navbar-menu li a {
   color: white;
   font-size: 16px;
   font-weight: bold;
   text-decoration: none;
 }
 
 .menu-navbar-menu li a:hover {
   text-decoration: underline;
 }
 